import React, { useState } from 'react'
import Button from '@mui/material/Button';

const Laptop = ({laptop}) => {
    const [isShown, setIsShown] = useState(false);

    const toggleDetails = () => {
        setIsShown(!isShown);
    }
    return (
        <>
        <h2>{laptop.name}</h2>
        { isShown && (
            <>
            <div>Brand: {laptop.brand}</div>
            <div>Weight: {laptop.weigth}</div>
            </>
        )}
        <Button variant="outlined" onClick={toggleDetails}>{ isShown ? "show less..." : "show more..."}</Button>
        </>
    )
}

export default Laptop