import React, { useEffect, useState } from "react"
import Laptop from './components/Laptop'
import LoadingMask from './components/LoadingMask'
import http from 'axios';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

const App = () => {

  const [laptops, setLaptops] = useState(false);
  const [sortOrder, setSortOrder] = useState("ASC");
  const [filterByName, setFilterByName] = useState("");

  useEffect(() => {
    const init = async () => {
      const response = await http.get('https://demoapi.com/api/laptop');
      setLaptops(response.data);
    };
    init();
  }, [])

  const sortByWeight = () => {

    const compare = ( a, b ) => {
      if ( a.weigth < b.weigth ) {
        if (sortOrder === "ASC") {
          return -1;
        }
        if (sortOrder === "DESC") {
          return 1;
        }
      }
      if ( a.weigth > b.weigth ) {
        if (sortOrder === "ASC") {
          return 1;
        }
        if (sortOrder === "DESC") {
          return -1;
        }
      }
      return 0;
    }
    let sortedLaptops = laptops.sort(compare);
    sortOrder === "ASC" ? setSortOrder("DESC") : setSortOrder("ASC");
    setLaptops(sortedLaptops);
  }

  return (
    <>
      <header>
        <h1>Laptops</h1>
        <Button variant="outlined" onClick={sortByWeight}>Sort</Button>
        <TextField type="text" name="filterByName" placeholder="filter" value={filterByName} onChange={(event) => setFilterByName(event.target.value)} />
      </header>
      { laptops ? 
          filterByName === "" ? 
            laptops.map((laptop, index) => <Laptop laptop={laptop} key={index} />) : 
            laptops.filter((item) => {
              if (item.name.includes(filterByName)) {
                return true
              } else {
                return false;
              }
            }).map((laptop, index) => <Laptop laptop={laptop} key={index} />)
         : <LoadingMask /> }
    </>
  )
}

export default App
